import { createRouter, createWebHistory } from "vue-router";

const routes = [
  {
    path: "",
    redirect: "/home",
  },
  {
    path: "/home",
    name: "home",
    component: () =>
    import(/* webpackChunkName: "home" */ "../views/home/index.vue"),
    meta: {
      title: "首页",
      keepActive: true,
      isShowTab:true
    },
  },
  {
    path: "/sic",
    name: "sic",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "sic" */ "../views/music/index.vue"),
      meta: {
        title: "音乐",
        keepActive: true,
        isShowTab:true
      },
  },
  {
    path: "/login",
    name: "login",
    component: () =>
      import(/* webpackChunkName: "login" */ "../views/login/index.vue"),
      meta: {
        title: "登录",
        keepActive: false,
        isShowTab:false
      },
  },
  {
    path: "/my",
    name: "my",
    component: () =>
      import(/* webpackChunkName: "my" */ "../views/my/index.vue"),
      meta: {
        title: "我的",
        keepActive: true,
        isShowTab:true
      },
  },
  {
    path: "/mv",
    name: "mv",
    component: () =>
      import(/* webpackChunkName: "mv" */ "../views/mv/index.vue"),
      meta: {
        title: "mv",
        keepActive: true,
        isShowTab:true
      },
  },
  {
    path: "/collect",
    name: "collect",
    component: () =>
      import(/* webpackChunkName: "collect" */ "../views/my/personnal/Collect.vue"),
      meta: {
        title: "收藏",
        keepActive: false,
        isShowTab:false
      },
  },
  {
    path: "/envelop",
    name: "envelop",
    component: () =>
      import(/* webpackChunkName: "envelop" */ "../views/my/personnal/Envelop.vue"),
      meta: {
        title: "意见",
        keepActive: false,
        isShowTab:false
      },
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
