import "core-js/modules/es.array.includes.js";
export default {
  data() {
    return {
      isShow: false,
      arr: ["/mv", "/my", "/home", "/sic"],
      tabbarList: [{
        name: "首页",
        url: "/home",
        icon: "home-o"
      }, {
        name: "音乐",
        url: "/sic",
        icon: "music-o"
      }, {
        name: "mv",
        url: "/mv",
        icon: "video-o"
      }, {
        name: "我的",
        url: "/my",
        icon: "contact"
      }]
    };
  },

  watch: {
    $route: function (to) {
      if (this.arr.includes(to.path)) {
        this.isShow = true;
      } else {
        this.isShow = false;
      }
    }
  },

  created() {}

};